.button {
  display: inline-block;
  width: 12.625rem;
  background-color: #408f95;
  text-align: center;
  padding-block: 0.9375rem;
  color: #fff;
  font-size: 0.875rem;
  font-family: "Gotu", sans-serif;
  line-height: 1.57143;
  position: relative;
  z-index: 1;
  transition: color 0.2s;
  margin-right: 0.1875rem;
  border: 1px solid #408f95;
}

.button::before {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

@media screen and (min-width: 768px) {
  .button:hover {
    color: #408f95;
  }
}

@media screen and (min-width: 768px) {
  .button:hover::before {
    transform: scale(1, 1);
  }
}

.button::after {
  position: absolute;
  content: "";
  border: 1px solid #408f95;
  top: 0.25rem;
  right: -0.25rem;
  width: 100%;
  height: 100%;
  z-index: -2;
}

.button p {
  display: inline-block;
  padding-right: 3.046875rem;
  position: relative;
}

.button p::after {
  position: absolute;
  top: 40%;
  right: 0;
  transform: translateY(-50%);
  width: 2.5625rem;
  height: 0.5rem;
  background: url(../../assets/images/common/button-arrow.png) no-repeat center center/contain;
  content: "";
}

@media screen and (min-width: 768px) {
  .button:hover p::after {
    background: url(../../assets/images/common/button-arrow-green.png) no-repeat center center/contain;
    z-index: 3;
  }
}

.button:active {
  transform: translate(0.25rem, 0.25rem);
}

.button:active::after {
  display: none;
}

.button--white {
  background-color: #fff;
  color: #408f95;
  border: 1px solid #fff;
}

.button--white p {
  padding-right: 3.5625rem;
}

@media screen and (min-width: 768px) {
  .button--white:hover {
    color: #fff;
  }
}

.button--white::before {
