[type=radio] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.375rem;
  font-size: 1rem;
  line-height: 1;
}
[type=radio] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background: #fff;
  border: 1px solid #408f95;
  width: 0.875rem;
  height: 0.875rem;
  display: block;
}
[type=radio] + span::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0.0625rem;
  display: block;
  opacity: 0;
  transition: 0.2s ease;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: translateY(-64%) rotate(-38deg);
  width: 0.6125rem;
  height: 0.35rem;
  margin-top: -0.05em;
  margin-left: 0.1em;
  border-left: 2px solid #c94800;
  border-bottom: 2px solid #c94800;
}
[type=radio]:checked + span::after {
  opacity: 1;
}
[type=radio]:hover + span::before, [type=radio]:focus + span::before {
  border: 1px solid #408f95;
  box-shadow: 0px 0px 8px rgba(73, 115, 255, 0.2);
}
[type=radio]:active + span::before {
  border: 1px solid #408f95;
  background: rgba(73, 115, 255, 0.2);
}

