.form__checkbox1 [type=checkbox] + span {
  display: inline-block;
  position: relative;
  padding: 0 0 0 1.5rem;
  font-size: 0.875rem;
}
@media screen and (min-width: 768px) {
  .form__checkbox1 [type=checkbox] + span {
    padding: 0 0 0 1.5rem;
  }
}
.form__checkbox1 [type=checkbox] + span::before {
  position: absolute;
  content: "";
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background: #fff;
  border: 1px solid #408f95;
  width: 0.875rem;
  height: 0.875rem;
  display: block;
}
.form__checkbox1 [type=checkbox] + span::after {
  content: "";
  position: absolute;
  top: 22%;
  left: 0.15625rem;
  display: block;
  opacity: 0;
  transition: 0.2s ease;
  /*チェックマークじゃない場合は以下を変更する*/
  transform: rotate(-45deg);
  width: 0.6125rem;
  height: 0.35rem;
  margin-top: 0.1em;
  margin-left: 0em;
  border-left: 2px solid #c94800;
  border-bottom: 2px solid #c94800;
}
.form__checkbox1 [type=checkbox]:checked + span::after {
  opacity: 1;
}
.form__checkbox1 [type=checkbox]:hover + span::before, .form__checkbox1 [type=checkbox]:focus + span::before {
  border: 1px solid #408f95;
  box-shadow: 0px 0px 5px rgba(73, 115, 255, 0.2);
}
.form__checkbox1 [type=checkbox]:active + span::before {
  border: 1px solid #408f95;
  background: rgba(73, 115, 255, 0.2);
}

