@use "global" as *;

.campaign__item {
	width: rem(280);

	@include mq("md") {
		width: rem(333);
	}
}

.campaign-card {
	// width: calc(280 / 375) * 100%;
	// width: rem(280);
	box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
	display: flex;
	flex-direction: column;
	height: auto;

	// @include mq("md") {
	// 	width: rem(333);
	// }
}

.campaign-card__image {
}

.campaign-card__image img {
	aspect-ratio: 280 / 188;
	width: 100%;
	height: 100%;
	object-fit: cover;

	@include mq("md") {
		aspect-ratio: 333 / 223;
	}
}

.campaign-card__image--side img {
	aspect-ratio: 294 / 188;

	@include mq("md") {
		aspect-ratio: 294 / 188;
	}
}

.campaign-card__contents {
	padding: rem(16) rem(23) rem(24);
	display: flex;
	flex-direction: column;
	flex-grow: 1;

	@include mq("md") {
		padding: rem(24) rem(16) rem(40);
	}
}

.campaign-card__contents--side {
	@include mq("md") {
		padding: rem(16) rem(23) rem(24);
	}
}

.campaign-card__head {
	flex-grow: 1;
}

.campaign-card__head--side {
	text-align: center;
}

.campaign-card__category {
	display: flex;
	flex-wrap: wrap;
	gap: rem(5);
}

.campaign-card__label {
	color: $color-green;
	font-size: rem(12);
	line-height: 1.8333;
	padding: rem(3) rem(11);
	border: 1px solid $color-green;
	display: inline-block;

	@include mq("md") {
		padding: rem(5) rem(15);
	}
}

// .campaign-card__label:not(:first-child) {
// 		margin-left: rem(10);
// }


.campaign-card__price-body {
	position: relative;
	margin-top: rem(8);
}
.campaign-card__price-body::before {
	position: absolute;
	content: "";
	background-color: $color-green;
	border-radius: 5px;
	width: 100%;
	height: rem(1);
	top: 0;
	left: 0;
}

.campaign-card__title {
	margin-top: rem(4);
	font-size: rem(16);
	font-weight: 500;
}

.campaign-card__title--side {
	margin-top: 0;
}

.campaign-card__price-info {
	text-align: center;
	margin-top: rem(8);
	font-size: rem(14);
	line-height: 1.7143;

	@include mq("md") {
		margin-top: rem(16);
		font-size: rem(16);
		line-height: 1.5;
	}
}

.campaign-card__price {
	margin-top: rem(8);
	display: flex;
	justify-content: center;
	align-items: baseline;
	gap: rem(16);
	font-family: "Lato", sans-serif;
	font-weight: 700;
	line-height: 1;

	@include mq("md") {
		margin-top: rem(7);
		gap: rem(24);
		//金額の桁数が減った時表記を左詰めにするかどうか
		// justify-content: flex-start;
		// margin-left: rem(18);
	}
}

.campaign-card__price-old {
	font-size: rem(20);
	position: relative;
	padding-right: rem(2); //赤色の取り消し線が右に少しはみ出ているため

	@include mq("md") {
		font-size: rem(24);
	}
}

.campaign-card__price-old--side {
	@include mq("md") {
		font-size: rem(20);
	}
}

.campaign-card__price-old::after {
	position: absolute;
	content: "";
	background-color: #c94800;
	width: 100%;
	height: rem(3);
	top: 45%;
	left: 50%;
	transform: translate(-50%, -51%);

	@include mq("md") {
		top: 50%;
	}
}

.campaign-card__price-new {
	font-size: rem(32);
	color: $color-green;

	@include mq("md") {
		font-size: rem(40);
	}
}

.campaign-card__price-new--side {
	@include mq("md") {
		font-size: rem(32);
	}
}

/*
下層ページのcampaign-card
--------------------------------*/
.campaign-card__image--sub img {
	@include mq("md") {
		aspect-ratio: 520 / 347;
	}
}

.campaign-card__contents--sub {
	@include mq("md") {
		padding: rem(24) rem(24) rem(44);
	}
}

.campaign-card__head--sub {
	flex-grow: 0;
}

.campaign-card__title--sub {
	@include mq("md") {
		font-size: rem(24);
	}
}

.campaign-card__price-body--sub {
	@include mq("md") {
		margin-top: rem(2);
	}
}

.campaign-card__price-info--sub {
	margin-top: rem(8);
	font-size: rem(14);

	@include mq("md") {
		margin-top: rem(24);
		font-size: rem(16);
	}
}

.campaign-card__price--sub {
	@include mq("md") {
		gap: rem(40);
		margin-top: rem(9);
	}
}

.campaign-card__price--side {
	gap: rem(16);

	@include mq("md") {
		gap: rem(16);
	}
}

.campaign-card__text {
	margin-top: rem(24);
	// flex: 1;
}

.campaign-card__info {
	margin-top: auto;
	text-align: center;
}

.campaign-card__dates {
	margin-top: rem(32);
	color: $color-green;
	font-weight: 700;
	line-height: 1.125;
}

.campaign-card__contact {
	margin-top: rem(7);
	color: $color-green;
	font-size: rem(14);
	line-height: 1.2857;
}

.campaign-card__button {
	margin-top: rem(15);
}
