	font-size: rem(14);
	color: #c94800;
	width: calc(224 / 345) * 100%;
	margin-inline: auto;
	padding-left:1em;
	text-indent:-1em;
	text-align: center;
	display: none;
}

.page-contact__error-message.is-error {
	display: block;
}

