@use "global" as *;

.single-post {
}

.single-post__date {
	display:inline-block;
	font-family: $english-font-family;
	font-size: 14px;
	line-height: 1.2857;
}

.single-post__title {
	margin-top: 8px;
	font-size: 24px;
	font-weight: 500;
	line-height: calc(35 / 24);
	position: relative;
	padding-bottom: rem(4);

	@include mq('md') {
		padding-bottom: 0;
		font-size: rem(32);
	}
}

.single-post__title::after {
	position: absolute;
	content: "";
	background-color: $color-green;
	width: 100%;
	height: rem(1);
	bottom: 0;
	left: 0;
}

.single-post__content {

