@use "global" as *;

.price-table {
	width: 100%;

	@include mq("md") {
		display: flex;
		justify-content: center;
		// margin-inline: auto;
		// width: rem(700);
	}
}

.price-table__head {
	background: $color-green;
	color: $color-white;
	padding-block: rem(8);
	text-align: center;

	@include mq('md') {
		// width: rem(200);
		width: calc(200 / 700) * 100%;
		display: grid;
		place-content: center;
		// padding-block: 0;
	}
}

.price-table__head span {
	padding-left: rem(32);
	position: relative;

	@include mq('md') {
		padding-left: 0;
		padding-bottom: rem(72);
	}
}

.price-table__head span::before {
	position: absolute;
	content: "";
	background: url(../../assets/images/common/price-icon.png) no-repeat center center / cover;
	width: rem(24);
	height: rem(24);
	top: 50%;
	transform: translateY(-50%);
	left: 0;
	color: $color-white;

	@include mq('md') {
		top: rem(32);
		// bottom: 0;
		left: 50%;
		transform: translateX(-47%);
		width: rem(64);
		height: rem(64);
	}
}

.price-table__body {
	width: 100%;

	@include mq('md') {
		// width: rem(500);
		width: calc(500 / 700) * 100%;
	}
}

.price-table__body, .price-table__body td {
	border: 1px solid $color-green;
	border-collapse: collapse;
}

.price-table__body td {
	padding: rem(6) rem(16);
	height: rem(60);
	line-height: 1.4;
	text-align: center;

	@include mq('md') {
		padding: rem(5) rem(16) rem(5) rem(14);
		height: rem(59);
	}
}

.price-table__body td:first-child {
	width: calc(240 / 345) * 100%;

	@include mq('md') {
		text-align: left;
		width: calc(339 / 500) * 100%;
	}
}

.price-table__body td:nth-child(2) {
	text-align: end;
}
